import React, { useEffect } from 'react'
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward'
import './Content.scss'
import { Link, Route, useLocation, Switch, useHistory } from 'react-router-dom'
import { AppContext } from '../App'

import Post from './Post'
import NotFoundPage from './NotFoundPage'
import Login from './Login'
import Quill from './Quill'
import Tags from './Tags'
import CommentLists from './CommentLists'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import MenuIcon from '@material-ui/icons/Menu'
import { resize, resizeTextarea, slideMenu } from '../utils/util'

function Content(props) {
  const store = React.useContext(AppContext)
  const location = useLocation() //hash ì‚¬ìš©
  const history = useHistory()

  useEffect(resize,[])

  useEffect(() => {
    const loading = document.querySelector('#loading')
    const views = document.querySelectorAll('.post')
    if (store.ready) {
      if (loading) loading.style.display = 'none'
      if (views) {
        for (let i of views) {
          i.style.display = 'block'
        }
      }
      setTimeout(function () {
        resizeTextarea()
      }, 10)
    } else {
      if (loading) loading.style.display = 'flex'
      if (views) {
        for (let i of views) {
          i.style.display = 'none'
        }
      }
    }
  }, [location.hash, store])

  const scrollUp = (e) => {
    // contentì˜ ìŠ¤í¬ë¡¤ì„ ê°€ì¥ ìœ„ë¡œ
    const body = document.body
    let contentScroll = body.scrollTop
    let dif = contentScroll / 50
    const frame = setInterval(function () {
      if (body.scrollTop <= 0) {
        clearInterval(frame)
      } else {
        body.scrollTop -= dif
      }
    }, 10)
  }

  return (
    <div id="content" className="slideMenu">
      {/* FABs */}
      {store.login && store.login.username === 'imki123' && (
        <Link id="postFAB" className="hover FAB" to="/quill">
          <AddCircleOutlineIcon />
        </Link>
      )}
      <div id="scrollFAB" className="hover FAB" onClick={scrollUp}>
        <ArrowUpwardIcon />
      </div>
      <div
        id="backFAB"
        className="hover FAB"
        onClick={() => {
          history.go(-1)
        }}
      >
        <ArrowBackIcon />
      </div>
      <div id="menuFAB" className="hover FAB" onClick={slideMenu}>
        <MenuIcon />
      </div>

      {/* ë¡œë”© */}
      <div id="loading">
        <img alt="Loading" src={process.env.PUBLIC_URL + '/images/loading.gif'} />
      </div>

      <Switch>
        <Route path={['/login', '/register', '/loginStatus', '/withdraw']} component={Login} />
        <Route path="/quill/:postId?" component={Quill} />
        <Route path="/tags/:tag" component={Tags} />
        <Route path="/comments" component={CommentLists} />
        <Route path="/" exact component={Post} />
        <Route path="/posts/:postId" component={Post} />
        <Route path="/NotFoundPage" component={NotFoundPage} />
        <Route path="*" component={NotFoundPage} />
      </Switch>

      {/* í…ìŠ¤íŠ¸ ì—ì–´ë¦¬ì–´ ë†’ì´ ì¡°ì •ì„ ìœ„í•œ ì•ˆë³´ì´ëŠ” ê°€ì§œ í…ìŠ¤íŠ¸ì—ì–´ë¦¬ì–´ */}
      <textarea disabled id="fakeTextarea" />
    </div>
  )
}
export default React.memo(Content)
